* {
  box-sizing: border-box;
}

body {
  margin: 0;
  line-height: 1.5;
  color: #333333;
  overflow-x: hidden;
}

.v-header {
  height: 100vh;
  display: flex;
  align-items: center;
  color: #fff;
  z-index: -1;
}

.cont {
  max-width: 960px;
  padding-left: 1rem;
  padding-right: 1rem;
  margin: auto;
  text-align: center;
}

.fullscreen-video-wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}

.fullscreen-video-wrap video {
  min-width: 100%;
  min-height: 100%;
  z-index: -1;
}

/* .header-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0.5;
} */

.header-content,
.header-link {
  z-index: 2;
}

.header-content h3 {
  font-size: 50px;
  margin-bottom: 0;
}

.button-style {
  background: white;
  color: black;
  font-size: 1.2rem;
  padding: 1rem 2rem;
  margin: 1rem 1rem;
  margin-top: 30px;
  text-decoration: none;
  z-index: 2;
}

.rowmove {
  margin-left: 10%;
}

@media (max-width: 960px) {
  .cont {
    padding-right: 3rem;
    padding-left: 3rem;
  }
}

.heropanel--video {
  font-family: "Open Sans", sans-serif;
  min-height: 320px;
}

@keyframes gm-slidein {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }

  to {
    -webkit-transform: none;
    opacity: 1;
    transform: none;
  }
}

.heropanel__content {
  -moz-animation: gm-slidein 3s 1;
  -ms-animation: gm-slidein 3s 1;
  -o-animation: gm-slidein 3s 1;
  -webkit-animation: gm-slidein 3s 1;
  animation: gm-slidein 3s 1;
  border-bottom: 1px solid #fff;
  margin: 0 auto;
  max-width: 50%;
  padding: 4em 0 2em;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
}

.heropanel__content h1 {
  margin: 0 0 0.5em;
  margin-top: 40px;
  text-transform: uppercase;
}

.heropanel__content h1 a {
  color: #fff;
  text-decoration: none;
}

.heropanel__content p {
  color: #fff;
  margin: 0;
  text-transform: uppercase;
}

.home-photo {
  z-index: -1;
  position: fixed;

  top: 0;
  left: 0;
  right: 0;
}
